/*#007bff*/
header .container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top:30px ;
    padding-bottom:30px ;
    position: relative;
}

header .container ul{
    display: flex;
    transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
}

header .container ul li{
    margin-left: 30px;
}

header .container ul li a{
    color: black;
}

header .container ul li a:hover{
    color: #007bff;
}

header .container ul li a.active{
    color: #007bff;
}

header .container .logo{
    font-size: 30px;
}

header .container .logo span{
color: #007bff;
}

header .container .fa-bars{
    font-size: 30px;
    display: none;
}

header .container ul .fa-times{
    display: none;
}

@media (max-width:768px) {
    
    header .container ul{
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 30px;
        background-color: white;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
        right: -100%;
        padding: 30px;
    }

    header .container ul.active{
        right: 0;
    }

    header .container ul li{
        margin-bottom: 20px;
    }

    header .container ul .fa-times{
        align-self: flex-end;
        display: block;
        font-size: 20px;
        cursor: pointer;
    }

    header .container .fa-bars{
        display: block;
        cursor: pointer;
    }
}