.home img{
    width: 50%;
}

.home .text{
    width: 50%;
}

.home .container{
    display: flex;
    align-items: center;
}

.home{
    padding: 100px 0 0;
}

.home p{
    color: #6c757d;
    line-height: 1.8;
}

.home button{
    padding: 15px 30px;
    border: none;
    outline: none;
    background-color: #007bff;
    color: white;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    margin-top: 50px;
    font-size: 14px;
    cursor: pointer;
    box-shadow: none;
    position: relative;
    top: 0;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.home button:hover{
    box-shadow: 0 10px 15px -9px #007bff;
    top: -2px;
}

@media (max-width:768px) {
    .home .container{
        flex-direction: column-reverse;
    }
    .home img{
        width: 100%;
    }
    
    .home .text{
        width: 100%;
        text-align: center;
        padding-top: 30px;
    }
    
}
.features .text{
    text-align: center;
    padding: 30px 0;
}

.features .text h2{
    font-size: 30px;
    margin: 20px 0;
}

.features .text p{
color: #6c757d;
}

.features .cont{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 50px;
    gap: 50px;
