}
.box{
    width: 100%;
    background-color: #f1f1f1;
    text-align: center;
    position: relative;
    padding: 10px;
    top: 0;
    box-shadow: none;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.box h3{
    font-weight: normal;
    margin: 15px 0;
}

.box p{
    line-height: 1.8;
    margin-bottom: 15px;
    font-size: 14px;
}

.box a{
    color: #007bff;
    font-size: 14px;
}

.box a:hover{
    color: #005ab9;
}

.box i{
    position: absolute;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: #4285f4;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    top: -15%;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.box:hover i{
    background-color: white;
    color: #4285f4;
}

.box:hover{
    background-color: #4285f4;
    color: white;
    top: -5px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.box:hover p{
    color: rgb(221, 221, 221);
}

.box:hover a{
    color: white;
}
