.features .text{
    text-align: center;
    padding: 30px 0;
}

.features .text h2{
    font-size: 30px;
    margin: 20px 0;
}

.features .text p{
color: #6c757d;
}

.features .cont{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 50px;
    margin: 100px auto;
}