footer .icons{
    text-align: center;
}

footer .icons i{
    padding: 50px 0;
    color:#007bff;
    cursor: pointer;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

footer .icons i:hover{
    color: #005ab9;
}

footer .icons i:not(:last-child){
    margin-right:40px;
}

footer p{
    text-align: center;
    padding: 10px 0;
}