.about h2{
    font-size: 30px;
    margin: 30px 0;
    text-align: center;
}

.about h3{
    font-size: 30px;
    margin: 30px 0;
}

.about img{
    width: 50%;
    margin-right: 20px;
}

.about .text{
    width: 50%;
}

.about .cont{
    display: flex;
}

.about{
    padding: 0 0 0;
}

.about p{
    color: #6c757d;
    line-height: 1.8;
}

.about ul{
    padding-left: 40px;
    margin-top: 40px;
}

.about ul li{
    color: #6c757d;
    margin: 15px 0;
    position: relative;
}

.about button{
    padding: 15px 30px;
    border: none;
    outline: none;
    background-color: #007bff;
    color: white;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    margin-top: 50px;
    font-size: 14px;
    cursor: pointer;
    box-shadow: none;
    position: relative;
    top: 0;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.about button:hover{
    box-shadow: 0 10px 15px -9px #007bff;
    top: -2px;
}

@media (max-width:768px) {
    .about .cont{
        flex-direction: column;
    }
    .about img{
        width: 100%;
        margin-right: 0;
    }
    
    .about .text{
        width: 100%;
        padding-top: 30px;
    }
    
}

.about ul li::before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f00c";
    position: absolute;
    left: -40px;
    color: #8bc34a;
}